import { request } from '@/utils/request';
import { REQUEST_SUCCEEDED_CODE, MainColor } from '@/common/constants';
import { REFRESH_USERINFO } from "@/common/globalMsgKeys";
/**
 * login 登录界面-用于微信登录
 * @description 登录界面-用于微信登录
 * @Author: 穆兰
 * @Date: 2022/1/5
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/10
 */
export default {
    data() {
        return {
            isLoading: false,
            agreest: false,
            MainColor
        };
    },
    methods: {
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         *@description 点击其他登录的文字
         */
        onOtherLoginTextClick() {
            uni.navigateTo({
                url: "./account-login"
            });
        },
        /**
         * @description 点击选择框
         */
        onCheckBoxClick() {
            this.agreest = !this.agreest;
            console.log("this.agreest", this.agreest);
        },
        /**
         * @description 点击用户协议文字
         */
        onUserProtocolClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-use-protocol/bugu-use-protocol"
            });
        },
        /**
         * @description 点击隐私政策文字
         */
        onPrivacyPolicyClick() {
            uni.navigateTo({
                url: "/pages/login/bugu-privacy-policy/bugu-privacy-policy"
            });
        },
        /**
         * @description 点击登录按钮
         */
        onLoginButtonClidk() {
            if (this.isLoading) {
                return;
            }
            if (this.agreest) {
                let _this = this;
                uni.login({
                    provider: 'weixin',
                    success: function (res) {
                        if (res.code) {
                            _this.code = res.code;
                            console.log(res.code);
                            try {
                                let BeInvitedCode = uni.getStorageSync('BeInvitedcode');
                                _this.tryToLogin(res.code, BeInvitedCode);
                                uni.setStorage({
                                    key: 'BeInvitedcode',
                                    data: null
                                });
                            }
                            catch {
                                _this.tryToLogin(res.code);
                            }
                        }
                        else {
                            console.log('登录失败！' + res.errMsg);
                        }
                    }
                });
            }
            else {
                uni.showToast({
                    title: '请先同意《用户协议》和《隐私政策》',
                    icon: 'none'
                });
            }
        },
        /**
         * @description 登录
         * @param code 微信登录获取的凭证
         * @param BeInvitedCode 他人的邀请码
         */
        async tryToLogin(code, BeInvitedCode) {
            uni.showLoading({
                title: '正在登陆'
            });
            this.isLoading = true;
            try {
                let res = await request({
                    data: {
                        method: 'GET',
                        group: 'user',
                        action: 'login',
                        data: BeInvitedCode ? {
                            inviteCode: BeInvitedCode,
                            code: code
                        } : {
                            code: code
                        },
                        header: {
                            'content-type': 'application/x-www-form-urlencoded' // 默认值
                        },
                    }
                });
                this.isLoading = false;
                if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                    console.log("登录成功");
                    await uni.setStorage({
                        key: 'token',
                        data: res.data.data.token
                    });
                    await uni.setStorage({
                        key: 'userInfo',
                        data: res.data.data.userInfo
                    });
                    console.log(res.data.data);
                    uni.showToast({
                        title: '登录成功',
                        icon: 'success'
                    });
                    uni.$emit(REFRESH_USERINFO, { needRefresh: true });
                    uni.navigateBack({});
                }
                else {
                    console.log(res);
                }
            }
            catch {
                uni.showToast({
                    title: '登录失败，请稍后再试',
                    icon: 'none'
                });
            }
        }
    }
};
